import React from "react";
import {
  Form,
  Input,
  Button,
  DatePicker,
  Select,
  Progress,
  Upload,
  Icon
} from "antd";

import "./style.css";

const { Option } = Select;
const { TextArea } = Input;

const formItemLayout = {
  labelCol: {
    xs: { span: 20 },
    sm: { span: 5 }
  },
  wrapperCol: {
    xs: { span: 20 },
    sm: { span: 16 }
  }
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0
    },
    sm: {
      span: 16,
      offset: 5
    }
  }
};

const config = {
  rules: [{ type: "object", required: true, message: "Please select time!" }]
};

const CreateReport = ({ form: { getFieldDecorator }, form }) => {
  const handleSubmit = e => {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("Received values of form: ", values);
      }
    });
  };

  const handleSelectChange = value => {
    console.log(value);
  };

  const normFile = e => {
    console.log("Upload event:", e);
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  return (
    <div className="create-report">
      <div className="create-report-title">To co bo</div>
      <Form {...formItemLayout} onSubmit={handleSubmit}>
        <Form.Item label="Tn bo co">
          {getFieldDecorator("ten-bao-cao", {
            rules: [
              {
                required: true,
                message: "Cn nhp tn bo co !"
              }
            ]
          })(<Input />)}
        </Form.Item>
        <Form.Item label="Ngy bo co">
          {getFieldDecorator(
            "date-picker",
            config
          )(<DatePicker placeholder={"YYYY/MM/DD"} />)}
        </Form.Item>
        <Form.Item label="Loi hnh kim tra">
          {getFieldDecorator("gender", {
            rules: [{ required: true, message: "Chn loi hnh kim tra !" }]
          })(
            <Select
              onChange={handleSelectChange}
              placeholder="Chn loi hnh kim tra"
            >
              <Option value="male">
                Kim tra cht lng quy trnh xng thuc nc th y
              </Option>
              <Option value="female">
                Kim tra cht lng quy trnh xng thuc bt th y
              </Option>
              <Option value="female">
                Kim tra cht lng quy trnh xng thc phm chc nng
              </Option>
              <Option value="female">Kim tra cht lng bao b cp 2</Option>
              <Option value="female">Kim tra cht lng nguyn liu</Option>
            </Select>
          )}
        </Form.Item>
        <Form.Item label="Ngi nh gi">
          {getFieldDecorator("x", {
            rules: [
              {
                required: true,
                message: "Cn nhp tn ngi nh gi !"
              }
            ]
          })(<Input />)}
        </Form.Item>
        <Form.Item label="Ngi thc hin kim tra">
          {getFieldDecorator("f", {
            rules: [
              {
                required: true,
                message: "Cn nhp tn ngi thc hin kim tra !"
              }
            ]
          })(<Input />)}
        </Form.Item>
        <Form.Item label="Cc tiu ch">
          {getFieldDecorator("h", {
            rules: [
              {
                required: true,
                message: "Cn nhp cc tiu ch !"
              }
            ]
          })(<TextArea />)}
        </Form.Item>
        <Form.Item label="Kt qu t c">
          {getFieldDecorator("s", {
            rules: [
              {
                required: true,
                message: "Cn nhp cc kt qu t c !"
              }
            ]
          })(<TextArea />)}
        </Form.Item>
        <Form.Item label="Ngi thc hin kim tra">
          {getFieldDecorator("p", {
            rules: [
              {
                required: true,
                message: "Cn nhp tn ngi thc hin kim tra !"
              }
            ]
          })(<Input />)}
        </Form.Item>
        <Form.Item label="Mc  hon thnh">
          {getFieldDecorator("w", {
            rules: [
              {
                required: true,
                message: "Cn nhp tn ngi thc hin kim tra !"
              }
            ]
          })(<Input placeholder="50%" />)}
        </Form.Item>
        <Form.Item label="Kt qu kim tra cht lng">
          {getFieldDecorator("k", {
            rules: [
              {
                required: true,
                message: "Cn nhp cc kt qu kim tra cht lng !"
              }
            ]
          })(<TextArea />)}
        </Form.Item>
        <Form.Item label="File nh km">
          {getFieldDecorator("upload", {
            valuePropName: "fileList",
            getValueFromEvent: normFile
          })(
            <Upload name="logo" action="/upload.do" listType="picture">
              <Button>
                <Icon type="upload" /> Click to upload
              </Button>
            </Upload>
          )}
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button type="primary" htmlType="submit">
            To
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default Form.create({ name: "create-report" })(CreateReport);
